<template>
  <svg
    :width="width + margin.left + margin.right"
    :height="height + margin.top + margin.bottom"
  >
    <g
      class="content"
      :transform="`translate(${margin.left}, ${margin.top})`"
    >
      <template v-for="(item, index) in items">
        <g
          class="pointer"
          :class="{ done: isDone(item) }"
          :transform="`translate(0, ${lineHeight * index})`"
          :key="item.rowId"
          @click="setDone(item)"
        >
          <g>
            <text
              class="task"
              :fill="scaleColor.fill(item.activityType)"
              :x="leftColumn - 2"
              :y="lineHeight - 10"
            >
              {{ item.orderNr }} {{ item.taskDescr }}
            </text>
          </g>
          <g :transform="`translate(${leftColumn}, 0)`">
            <rect
              class="task"
              x="0"
              y="0"
              :height="lineHeight - 1"
              :width="width - leftColumn"
            />
            <line
              class="callout"
              :x1="5"
              :y1="lineHeight / 2"
              :x2="margin.left + scale.x(isTemplate ? (-item.yrBefore) : item.calculatedDate)"
              :y2="lineHeight / 2"
            />
            <circle
              class="milestone"
              r="5"
              :cx="margin.left + scale.x(isTemplate ? (-item.yrBefore) : item.calculatedDate)"
              :cy="lineHeight / 2"
              :fill="scaleColor.fill(item.activityType)"
            >
              <title v-if="item.calculatedDate">{{ formatDate(item.calculatedDate) }}</title>
            </circle>
            <text
              v-if="item.calculatedDate"
              class="dateText"
              :x="margin.left + 10 + scale.x(item.calculatedDate)"
              y="20"
            >
              {{ formatDate(item.calculatedDate) }}
            </text>
          </g>
        </g>
      </template>
      <g
        v-if="items.length > 0"
        :transform="`translate(${leftColumn}, 0)`"
      >
        <line
          class="today"
          :x1="scale.x(new Date())"
          :x2="scale.x(new Date())"
          :y1="0"
          :y2="height"
        />
      </g>
    </g>
    <g :transform="`translate(${margin.left + leftColumn}, ${margin.top})`">
      <g class="gridlines" ref="gridlines"></g>
    </g>
    <g
      class="legend"
      :transform="`translate(
          ${(width + margin.left + margin.right - scaleColor.fill.domain().length * 50) / 2},
          ${margin.top + lineHeight * items.length}
        )`"
    >
      <template v-for="(activityType, index) in scaleColor.fill.domain()">
        <g :transform="`translate(${index * 50}, ${margin.bottom - 20})`" :key="index">
          <circle cx="5" cy="0" r="4" :fill="scaleColor.fill(activityType)" />
          <text x="12" y="4">{{ activityType }}</text>
        </g>
      </template>
    </g>
  </svg>
</template>

<script>
import d3 from '@/assets/d3';

export default {
  props: ['items', 'isTemplate', 'doneItems'],
  data() {
    return {
      lineHeight: 30,
      leftColumn: 300,
      width: 800,
      margin: {
        top: 40,
        bottom: 40,
        left: 20,
        right: 20,
      },
    };
  },
  computed: {
    height() {
      return this.lineHeight * this.items.length;
    },
    scale() {
      if (this.isTemplate) {
        const x = d3.scaleLinear()
          .rangeRound([0, this.width - this.leftColumn - this.margin.left - this.margin.right])
          .domain([-5, 0]);
        return { x };
      }
      const dates = this.items.map(item => item.calculatedDate);
      const x = d3.scaleTime()
        .rangeRound([0, this.width - this.leftColumn - this.margin.left - this.margin.right])
        .domain([Math.min(...dates), Math.max(...dates)]);
      return { x };
    },
    scaleColor() {
      const activityTypes = this.items.map(item => item.activityType);
      const fill = d3.scaleOrdinal(d3.schemeCategory10)
        .domain(activityTypes);
      return { fill };
    },
  },
  watch: {
    scale() {
      d3.select(this.$refs.gridlines)
        .call(d3.axisTop(this.scale.x)
          .tickSize(-this.height));
    },
  },
  methods: {
    formatDate(dt) {
      return dt.toLocaleString();
    },
    setDone(item) {
      this.$emit('onSetDone', item);
    },
    isDone(item) {
      return this.doneItems.includes(item);
    },
  },
};
</script>

<style scoped>
  svg {
    font-family: Helvetica, Arial, sans-serif;
    font-size: 10pt;
  }
  g>g>rect.task {
    fill:lemonchiffon;
    opacity: 0;
    transition: opacity .5s;
  }
  g:hover>g>rect.task {
    opacity: 1;
  }
  g>g>text.dateText {
    fill:black;
    opacity: 0;
    transition: opacity .5s;
  }
  g:hover>g>text.dateText {
    opacity: 1;
  }
  text.task {
    text-anchor: end;
  }
  .done text.task {
    text-decoration-line: line-through;
  }
  line.today {
    stroke: orangered;
    opacity: .3;
  }
  g>g>line.callout {
    stroke: gray;
    stroke-width: 1;
    stroke-dasharray: 2, 2;
    opacity: 0;
    transition: opacity .5s;
  }
  g:hover>g>line.callout {
    opacity: 1;
  }
  g.done {
    opacity: .4;
  }
</style>

<style>
  .gridlines .tick line {
    stroke: #bbb;
    stroke-dasharray: 2, 2;
  }
  .gridlines path {
    display: none;
  }
  .gridlines text {
    fill: #888;
  }
</style>



