import axios from 'axios';

const GRAPHQL_URL = 'http://localhost:5000/graphql';

export default {
  getTasksByTemplateId(templateId) {
    const query = `
      query taskQuery($templateId: Int)
      {
        allTasks(
          condition: {
            templateId: $templateId
          }
          orderBy: ORDER_NR_ASC
        ) {
          nodes {
            rowId
            templateId
            orderNr
            taskDescr
            yrBefore
            activityType
            resmgrId
          }
        }
      }
    `;
    const variables = { templateId };
    return axios.post(GRAPHQL_URL, { query, variables })
      .then(response => response.data.data.allTasks.nodes);
  },
  getAllProjects() {
    const query = `
      {
        allStartDates {
          nodes {
            id: rowId
            projectDescr
            startDate
          }
        }
      }
    `;
    return axios.post(GRAPHQL_URL, { query })
      .then(response => response.data.data.allStartDates.nodes);
  },
  getTasksByProjectId(projectId) {
    const query = `
      query tasksByProjectId($projectId: Int)
      {
        allStartDates (
          condition: {
            rowId: $projectId
          }
        ) {
          nodes {
            id: rowId
            projectDescr
            startDate
            templateId
            templateByTemplateId {
              id: rowId
              descr
              tasksByTemplateId {
                nodes {
                  id: rowId
                  templateId
                  orderNr
                  taskDescr
                  yrBefore
                  activityType
                }
              }
            }
          }
        }
      }
    `;
    const variables = { projectId };
    return axios.post(GRAPHQL_URL, { query, variables })
      .then(response => response.data.data.allStartDates.nodes[0]);
  },
};



