<template>
  <div>
    <template v-if="this.project.id">
      <h1>{{ this.project.projectDescr }}</h1>
      <h2>{{ this.project.templateByTemplateId.descr }}</h2>
      <h3>Start date: {{ this.startDateFormatted }}</h3>
    </template>
    <div>
      <timeline-component
        :items="items"
        :doneItems="doneItems"
        @onSetDone="setDone"
      />
    </div>
    <b-table
      small hover
      :items="items"
      :fields="fields"
    >
    </b-table>
  </div>
</template>

<script>
import { DateTime } from 'luxon';
import TaskService from '@/services/task-service';
import TimelineComponent from '@/components/TimelineComponent';

export default {
  data() {
    return {
      taskService: TaskService,
      projectId: this.$route.params.id,
      project: {},
      doneItems: [],
      fields: [
        {
          key: 'orderNr',
          sortable: true,
        },
        {
          key: 'taskDescr',
          sortable: true,
        },
        {
          key: 'yrBefore',
          sortable: true,
        },
        {
          key: 'activityType',
          sortable: true,
        },
        {
          key: 'calculatedDate',
          sortable: true,
          formatter: dt => dt.toLocaleString(),
        },
      ],
    };
  },
  created() {
    this.taskService.getTasksByProjectId(this.projectId).then((data) => {
      this.project = data;
    });
  },
  computed: {
    startDateFormatted() {
      return DateTime.fromISO(this.project.startDate).toLocaleString();
    },
    items() {
      if (!this.project.templateByTemplateId) {
        return [];
      }
      const items = this.project.templateByTemplateId.tasksByTemplateId.nodes;
      return items.map(item =>
        Object.assign(
          {},
          item,
          {
            calculatedDate: DateTime
              .fromISO(this.project.startDate)
              .minus({ years: item.yrBefore }),
          },
        ),
      );
    },
  },
  methods: {
    setDone(item) {
      if (this.doneItems.includes(item)) {
        const index = this.doneItems.indexOf(item);
        this.doneItems.splice(index, 1);
      } else {
        this.doneItems.push(item);
      }
    },
  },
  components: {
    'timeline-component': TimelineComponent,
  },
};
</script>




